// src/components/layout/Sidebar.tsx - Fixed version
'use client';

import { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
    LayoutDashboard,
    Users,
    CreditCard,
    Settings,
    LogOut,
    BarChart3,
} from 'lucide-react';
import {
    Navbar,
    NavbarContent,
    NavbarMenu,
    NavbarMenuItem,
    NavbarMenuToggle,
} from '@heroui/navbar';
import { Button } from '@heroui/button';
import { Avatar } from '@heroui/avatar';
import { Badge } from '@heroui/badge';
import { cn } from '@/lib/utils';

const navItems = [
    { href: '/dashboard', icon: LayoutDashboard, label: 'Dashboard' },
    { href: '/providers', icon: Users, label: 'Providers' },
    { href: '/billing', icon: CreditCard, label: 'Billing' },
];

export function Sidebar() {
    const pathname = usePathname();
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    return (
        <>
            {/* Mobile Navbar */}
            <Navbar
                maxWidth="full"
                className="lg:hidden bg-background/80 backdrop-blur-md border-b border-divider"
                isMenuOpen={isMenuOpen}
                onMenuOpenChange={setIsMenuOpen}
            >
                <NavbarContent justify="start">
                    <NavbarMenuToggle
                        aria-label={isMenuOpen ? 'Close menu' : 'Open menu'}
                        className="lg:hidden"
                    />
                    <div className="flex items-center gap-2">
                        <BarChart3 className="h-6 w-6 text-primary" />
                        <span className="text-lg font-bold">USSD Portal</span>
                    </div>
                </NavbarContent>

                <NavbarMenu className="pt-8">
                    {navItems.map((item) => {
                        const Icon = item.icon;
                        const isActive = pathname === item.href;

                        return (
                            <NavbarMenuItem key={item.href}>
                                <Link
                                    href={item.href}
                                    className={cn(
                                        'w-full flex items-center gap-3 px-4 py-3 rounded-xl transition-colors',
                                        isActive
                                            ? 'bg-primary text-primary-foreground'
                                            : 'hover:bg-default-100'
                                    )}
                                    onClick={() => setIsMenuOpen(false)}
                                >
                                    <Icon className="h-5 w-5" />
                                    {item.label}
                                    {isActive && (
                                        <div className="ml-auto w-2 h-2 bg-primary-foreground rounded-full" />
                                    )}
                                </Link>
                            </NavbarMenuItem>
                        );
                    })}
                </NavbarMenu>
            </Navbar>

            {/* Desktop Sidebar */}
            <div className="hidden lg:flex lg:w-72 lg:flex-col lg:fixed lg:inset-y-0 z-50">
                <div className="flex flex-col flex-grow bg-background border-r border-divider pt-8 pb-4 overflow-y-auto">
                    <div className="flex items-center px-6 mb-10">
                        <BarChart3 className="h-8 w-8 text-primary" />
                        <span className="ml-3 text-2xl font-bold bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
                            USSD Portal
                        </span>
                    </div>

                    <nav className="flex-1 px-4 space-y-2">
                        {navItems.map((item) => {
                            const Icon = item.icon;
                            const isActive = pathname === item.href;

                            return (
                                <Link
                                    key={item.href}
                                    href={item.href}
                                    className={cn(
                                        'flex items-center px-4 py-3 text-sm font-medium rounded-xl transition-all duration-200',
                                        isActive
                                            ? 'bg-primary text-primary-foreground shadow-lg shadow-primary/25'
                                            : 'text-foreground-600 hover:bg-default-100'
                                    )}
                                >
                                    <Icon className={cn(
                                        'mr-3 h-5 w-5',
                                        isActive ? 'text-primary-foreground' : 'text-default-400'
                                    )} />
                                    {item.label}
                                    {isActive && (
                                        <div className="ml-auto w-2 h-2 bg-primary-foreground rounded-full" />
                                    )}
                                </Link>
                            );
                        })}
                    </nav>

                    <div className="px-4 mt-8 space-y-2">
                        <Button
                            fullWidth
                            variant="light"
                            className="justify-start text-foreground-600"
                            startContent={<Settings className="h-5 w-5" />}
                        >
                            Settings
                        </Button>
                        <Button
                            fullWidth
                            variant="light"
                            color="danger"
                            className="justify-start"
                            startContent={<LogOut className="h-5 w-5" />}
                        >
                            Logout
                        </Button>
                    </div>
                </div>
            </div>
        </>
    );
}